import { Component, OnInit } from '@angular/core';
import { fromEvent } from 'rxjs'; //引入

@Component({
  selector: 'app-back-top',
  templateUrl: './back-top.component.html',
  styleUrls: ['./back-top.component.css'],
})
export class BackTopComponent implements OnInit {
  subscribeScoll: any;
  scrollDis: any = {
    _top: 0, // 需要监听什么值就加什么值
  };

  constructor() {}

  ngOnInit(): void {
    this.subscribeScoll = fromEvent(window, 'scroll').subscribe((event) => {
      this.onWindowScroll(); //调用
    });
  }

  /* 监听滚动事件 */
  onWindowScroll() {
    this.scrollDis._top = this.scollPostion().top;
    // console.log(this.scrollDis._top);
    // 显示返回顶部小火箭
    if (this.scrollDis._top > 60) {
      var arr = document.querySelector('#backTop');
      arr.setAttribute('style', 'bottom: 50px; opacity: 1;');
    } else {
      var arr = document.querySelector('#backTop');
      arr.setAttribute('style', 'bottom: -50px; opacity: 0;');
    }
  }
  scollPostion() {
    var t, l, w, h;
    if (document.documentElement && document.documentElement.scrollTop) {
      t = document.documentElement.scrollTop;
      l = document.documentElement.scrollLeft;
      w = document.documentElement.scrollWidth;
      h = document.documentElement.scrollHeight;
    } else if (document.body) {
      t = document.body.scrollTop;
      l = document.body.scrollLeft;
      w = document.body.scrollWidth;
      h = document.body.scrollHeight;
    }
    return {
      top: t,
      left: l,
      width: w,
      height: h,
    };
  }

  // 返回顶部
  backToTop() {
    // 周期性定时器
    var timer = setInterval(function () {
      // 设置一个名为timmer的定时器，每10毫秒执行一次
      // var distance=document.documentElement.scrollTop||document.body.scrollTop;// 两种方法都可以获取但第一种可能存在兼容性问题
      var distance = document.documentElement.scrollTop;
      // 越滚越慢
      // var speed = distance / 20;
      // document.documentElement.scrollTop=distance-speed;

      //匀速滚
      document.documentElement.scrollTop = distance - 30;

      // 清除定时器
      if (distance === 0) {
        clearInterval(timer);
      }
    }, 10);
  }
}
